@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  scroll-behavior: smooth;
  box-sizing: border-box;
  --bullet-radius: 1rem;

  --bullet-r: calc(var(--bullet-radius) * 0.75);
  --bs-nav-link-hover-color: #ccaf52;
  --black: #000000;
  --yellow: #d1ff67;
  --white: #ffffff;
}
.logo {
  width: 3rem;
  height: 3rem;
}
