section.div-exp {
  /* width: 50%; */
  position: relative;
}
section.div-exp-right::before {
  position: relative;
  display: block;
  left: -2.3rem;
  top: calc(var(--bullet-radius) * -3.5);
  content: "";
  background-color: red;
  width: var(--bullet-r);
  height: var(--bullet-r);
  border-radius: 50vw;
}

section.div-exp-right {
  /* width: 50%; */
  position: relative;
}

.perpendicular {
  transform: rotate(90deg);
}

div.circle {
  width: var(--bullet-radius);
  height: var(--bullet-radius);
  background-color: lightcoral;
  border-radius: 50%;
  top: 3rem;
  left: -0.6rem;
  position: relative;
}
div.left-circle {
  width: var(--bullet-radius);
  height: var(--bullet-radius);
  background-color: lightcoral;
  border-radius: 50%;
  top: 3rem;
  left: calc(50vw - 6.1rem);
  z-index: 12312;
  position: relative;
}

header {
  background: var(--yellow);
}
body {
  background: var(--yellow);
  /* background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab); */
  background-size: 400% 400%;
  /* animation: gradient 15s ease infinite; */
  height: 100vh;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.navigation {
  border-left: 3px solid #0000006e;
  max-height: 85vh;
  position: sticky;
  top: 8rem;
}

.nav-link:active {
  background: linear-gradient(
    to right,
    rgba(0, 0, 255, 0.086) 50%,
    transparent 50%
  );
  background-size: 200% 100%;
  border-left: 1px solid black;
  background-position: left bottom;
}

.nav-link {
  text-transform: uppercase;
  background: linear-gradient(
    to right,
    rgba(0, 0, 255, 0.086) 50%,
    transparent 50%
  );
  background-size: 200% 100%;
  background-position: right bottom;
  transition: background-position 2s ease;
}

.selected,
.nav-link:hover {
  background-position: left bottom;
  /* border-left: 2px solid black; */
}
.nav-link li a {
  color: white;
}
.nav-link li a:visited {
  color: red;
}
/* 
  .nav-link:hover {
    padding-left: 5px;
    border-left: 3px solid red;
  } */
@keyframes grow {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes btnWavesBottom {
  from {
    transform: translateY(0);
    opacity: 1;
  }

  to {
    transform: translateY(2rem);
    opacity: 0;
  }
}

@keyframes btnWavesTop {
  from {
    transform: translateY(0);
    opacity: 1;
  }

  to {
    transform: translateY(-2rem);
    opacity: 0;
  }
}

.hero-image {
  border-radius: 50%;
  position: relative;
}
.hero-image::after {
  content: "";
  position: absolute;
  transition-duration: 500ms;
  top: 0;
  left: 0;
  width: 80%;
  height: 100%;
  background-color: red;
  z-index: 4;
}
.waves-container,
.waves-container-2 {
  position: relative;
}
.waves-container::before,
.waves-container-2::before {
  content: "";
  position: absolute;
  transition-duration: 500ms;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--black);
  /* transform: rotate(2.5deg); */
  z-index: -1;
  border-radius: 50%;
  border-top: 1px solid black;
  display: block;
  animation: 1.1s linear infinite btnWavesTop;
  border-radius: 50%;
  position: absolute;
}
.waves-container::after,
.waves-container-2::after {
  content: "";
  position: absolute;
  transition-duration: 500ms;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--black);
  z-index: -1;
  border-radius: 50%;
  border-top: 1px solid black;
  display: block;
  animation: 1.1s linear infinite btnWavesBottom;
  border-radius: 50%;
  position: absolute;
}
.waves-container-2::before,
.waves-container-2::after {
  animation-delay: 0.6s;
}

.title {
  font-size: 8rem;
}

.typewriter-container {
  display: flex;
  align-items: center;
  min-height: 5rem;
  font-size: 2rem;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  /* border-right: 0.15em solid orange;
    animation: blink-caret 0.75s step-end infinite; */
}
.title-text {
  display: inline-block;
  border-right: 0.1em solid orange; /* The cursor */
  animation: blink-caret 0.75s step-end infinite;
  font-family: "Poppins", sans-serif;
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

/* About Section */
.about-section {
  padding-top: 8rem;
  padding-left: 2rem;
}
.section-title-overlay-text {
  transform: translate3d(0px, -1.3087%, 0px);
  color: var(--black);
  font-family: Poppins;
  font-size: 10rem;
  font-style: normal;
  font-weight: 800;
  line-height: 120%;
  text-transform: uppercase;
  opacity: 0.05;
  position: absolute;
  padding-left: 1rem;
  /* 
    top: 0;
    text-align: left;
    left: 0; */
}
.section-title {
  border-left: 0.5rem solid var(--black);
  border-radius: 0.2rem;
  padding: 7rem 0 1rem 3.75rem;
}
.section-title h2 {
  font-size: 5rem;
  font-weight: 600;
  text-transform: capitalize;
}
.about-desc h2 {
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.about .experience-card {
  border: 1px solid var(--black);
  width: 100%;
  padding: 40px;
  text-align: center;
  transition-duration: 500ms;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--yellow);
  position: relative;
  z-index: 4;
}

.about .experience-card::before {
  content: "";
  position: absolute;
  transition-duration: 500ms;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--black);
  transform: rotate(2.5deg);
  z-index: -1;
}

.about .experience-card::after {
  content: "";
  position: absolute;
  transition-duration: 500ms;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--black);
  transform: rotate(-2.5deg);
  z-index: -1;
}

.about .experience-card .card-inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--yellow);
  z-index: 0;
}

.about .experience-card:hover::before,
.about .experience-card:hover::after {
  transform: rotate(0);
  opacity: 0;
}

.about .experience-card:hover .numbers .number-outline-one,
.about .experience-card:hover .numbers .number-outline-two {
  transform: rotate(0);
  opacity: 0;
}

.about .experience-card p {
  position: relative;
  z-index: 1;
}

.about .experience-card .numbers {
  z-index: 1;
  position: relative;
}

.about .experience-card .numbers span {
  font-size: 200px;
  font-weight: 700;
  line-height: 120%;
}

.about .experience-card .numbers .number-outline-one,
.about .experience-card .numbers .number-outline-two {
  position: absolute;
  top: 0;
  -webkit-text-stroke: 1px #000;
  -webkit-text-fill-color: rgba(0, 0, 0, 0);
  display: flex;
  justify-content: center;
  transition-duration: 500ms;
}

.about .experience-card .numbers .number-outline-one {
  transform: rotate(6deg);
}

.about .experience-card .numbers .number-outline-two {
  transform: rotate(-6deg);
}

@media only screen and (max-width: 600px) {
  .section-title-overlay-text {
    font-size: 3.5rem;
  }
  .section-title h2 {
    font-size: 2rem;
    font-weight: 600;
    text-transform: capitalize;
  }
}
